import assign from 'object-assign';
import {buildFuzzySearchPattern} from '../util';
import Class from '../core/Class';
import Module from '../core/Module';
import View from '../core/View';
import Events from '../core/Events';

var guid = Math.guid(),
    defaults = {
        label: 'Filter',
        hint: '',
        placeholder: '',
        queryNode: '> li',
        template: 'filterForm',
        fieldSelector: '.SearchForm-field',
        noResultsSelector: '.SearchForm-noResults',
        clearButtonSelector: '.SearchForm-clear'
    },
    option;

var FilteredSearch = ( Class.extend( Module, Events ) ).extend( {

    init: function ( el, opt ) {
        option = assign( {}, defaults, opt );

        this.$el = $( el );
        this.$nodeList = this.$el.find( option.queryNode );
        this.searchFormView = new View( option.template );

        this.searchFormView.whenTemplatesLoaded.then( () => {
            var tmpl = this.searchFormView.render( {
                guid: guid,
                label: option.label,
                hint: option.hint,
                placeholder: option.placeholder
            } );

            this.$form = $( tmpl );
            this.$searchField = this.$form.find( option.fieldSelector );
            this.$noResults = this.$form.find( option.noResultsSelector );
            this.$form.insertBefore( this.$el );
            this.focus();

            this.$form.on( 'keyup', option.fieldSelector, this.handleKeyEvent.bind( this ) );
            this.$form.on( 'keypress', option.fieldSelector, this.handleKeyEvent.bind( this ) );
            this.$form.on( 'click', option.clearButtonSelector, this.clearResults.bind( this ) );
        } );
    },

    clearResults: function () {
        this.$nodeList.show();
        this.$searchField.val( '' ).focus();
        this.toggleNoResults( false );
    },

    focus: function () {
        this.$searchField.focus();
    },

    blur: function () {
        this.$searchField.blur();
    },

    toggleNoResults: function ( doShow ) {
        this.$noResults.toggleClass( 'hidden', !doShow );
    },

    search: function ( query ) {
        var query = query.toLowerCase(),
            queryPattern = buildFuzzySearchPattern( query ),
            prevQuery = query.substr( 0, query.length - 1 );

        // Filter Logic, each result set, filtered based on previous result set
        this._filtered = this._filtered || {};

        var resultSets = (this._filtered[query] !== undefined)
            ?
            this._filtered[query]
            :
            this._filtered[query] = this.filterResultSets( this._filtered[prevQuery] || this.$nodeList, queryPattern );

        return resultSets;
    },

    filterResultSets: function ( list, pattern ) {
        return list.filter( function ( i, item ) {
            return pattern.test( item.innerText || item.textContent );
        } );
    },

    // note: not implemented
    highlightQueryInTemplate: function ( tmpl, target, query ) {

        var keywords = query.toLowerCase().split( ' ' );

        // refactor: cleanup logic - move to separate methods
        var $tmpl = $( tmpl ),
            namenode = $tmpl.find( target ),
            txt = namenode.text(),
            highlightHtml = '';

        for ( var i = 0; i < keywords.length; i++ ) {
            var keyword = keywords[i],
                searchIndex = txt.search( buildFuzzySearchPattern( keyword ) ),
                j = searchIndex,
                q = keyword.split( '' ),
                matches = 0;

            // ignore empty keyword artifacts from keywords.split( ' ' )
            if ( '' === keyword ) {
                break;
            }

            // prepend from previous fragment
            highlightHtml += txt.slice( 0, searchIndex );

            // start highlighting from search index
            while ( q[matches] ) {
                if ( q[matches] === txt[j].toLowerCase() ) {
                    highlightHtml += '<b>' + txt[j] + '</b>';
                    matches++;
                }
                else {
                    highlightHtml += txt[j];
                }
                j++;
            }

            // extract remainder of string for next keyword search
            txt = txt.slice( j, txt.length );
        }

        // append remainder of string
        highlightHtml += txt;
        namenode.html( highlightHtml );

        return $tmpl;
    },

    handleKeyEvent: function ( e ) {
        if ( e.which === 13 ) {
            e.preventDefault();
            return false;
        }

        // hide all, reveal valid results
        var results = this.search( this.$searchField.val().trim() ).show();
        var visibleNodes = this.$nodeList.not( ':hidden' ).not( results ).hide();
        this.toggleNoResults( !results.length );
    }
} );

export default FilteredSearch;



