import '../css/checkout.scss';

import Class from './modules/core/Class';
import FilteredSearch from 'filter/simpleListFilter';
import Module from './modules/core/Module';
import Events from './modules/core/Events';

var init = function ( form ) {
        // handle custom printing module
        this.disabled = false;

        // add validation to PO field
        $( '[id*="PONumber_TextBox"]' ).attr( 'maxlength', '50' );

        if ( window.location.pathname.indexOf( 'confirmation' ) !== -1 ) {
            $( 'body' ).removeClass( 'checkoutProcess' );
        }

        // preload loading gifs, posting form blocks additional http requests preventing these from displaying
        var imgs = ['upi_loading_small.gif', 'upi_loading_large.gif'];
        var img = new Image();
        for ( var i = 0; i < imgs.length; i++ ) {
            img.src = upi.paths.ORIGIN_CDN + '/images/' + imgs[i];
        }

        var self = this;
        $(
            this.proxy( function () {
                // wrap address lists in order to prepend filter form when needed
                this.address_containers
                    .find( '.addresses' )
                    .wrap( '<div class="addressList"></div>' )
                    .show();

                this.update_elements();

                this.bind( 'cart:update', this.proxy( this.update ) );
                this.bind( 'cart:submit', this.proxy( this.submit ) );

                // adding "cancel' class to update buttons prevents validation cart updates
                this.update_buttons.addClass( 'cancel' );

                this.el.validate( {
                    rules: self.validate_fields
                } );

                this.toggle_custom_printing();
                this.address_lists.hide();

                // lazy load address filters only once per address list
                this.address_lists.each( function ( i, list ) {
                    var $list = $( list );

                    $list.on( 'addresslist:open addresslist:close', function ( data ) {
                        data.list.parent( '.module' ).scrollToElement();
                    } );

                    $list.one( 'addresslist:open', function () {
                        if ( $list.find( 'address' ).length > 9 ) {
                            var addressFilter = new FilteredSearch(
                                $list.find( 'ul' ),
                                {
                                    label: 'Filter Addresses',
                                    hint: 'Anaheim, 90620, or Clementine St',
                                    placeholder: 'City, ZIP Code, or Street Name'
                                }
                            );

                            $list.on( 'addresslist:close', function ( e ) {
                                addressFilter.clearResults();
                                addressFilter.blur();
                            } );
                            $list.on( 'addresslist:open', function ( e ) {
                                addressFilter.focus();
                            } );
                        }
                    } );
                } );

                var handle_pageshow = self.proxy( function ( e ) {
                    // re-enable submit buttons when navigating away from page
                    this.trigger( 'checkoutpageshow' );
                } );

                $( window ).on( 'pageshow', handle_pageshow );
            } )
        );
    },
    toggle_buttons = function ( label, doDisable ) {
        doDisable = typeof doDisable !== 'undefined' ? doDisable : true;

        this.disabled = doDisable;

        $.each(
            [
                this.shipping_buttons,
                this.shipping_address_buttons,
                this.billing_address_buttons,
                this.submit_buttons,
                this.update_buttons
            ],
            function ( i, item ) {
                item[doDisable ? 'addClass' : 'removeClass']( 'disabled' );
            }
        );

        if ( label ) {
            label.target.attr( 'value', label.text );
        }
    },
    toggle_custom_printing = function () {
        var isChecked = this.custom_print_checkbox.is( ':checked' );
        this.custom_print_container.toggleClass( 'hidden', !isChecked );
    },
    enable_submit = function () {
        this.toggle_buttons( null, false );
    },
    handle_address_change = function ( e ) {
        var $button = $( e.currentTarget ),
            $list = $button.siblings( '.addressList:first' );

        this.toggle_address_list( $list );
    },
    toggle_address_list = function ( $list ) {
        var isVisible = $list.is( ':visible' );

        $list[isVisible ? 'slideUp' : 'slideDown']( 200 )
            .siblings( '.button' )
            .text( isVisible ? 'Change' : 'Cancel' );

        $list.trigger( {
            type: 'addresslist:' + (!isVisible ? 'open' : 'close'),
            list: $list
        } );
    },
    select_address_item = function ( e ) {
        var $selectedAddress = $( e.currentTarget ),
            $address = $( '#' + $selectedAddress.attr( 'name' ) + 'Address' ),
            $container = $selectedAddress.parents( '.module' );

        $selectedAddress
            .parents( 'li' )
            .find( 'address span' )
            .each( function ( i, field ) {
                var $field = $( field );
                $address.find( '.' + $field.attr( 'class' ) ).text( $field.text() );
            } );

        $address.highlight( '#fff9e4' );

        this.toggle_address_list( $address.next() );
        $container.find( 'a.button' ).text( 'Saving...' ).addClass( 'processing' );
    },
    handle_pageshow = function () {
        this.enable_submit();
    },
    handle_delete_cart = function ( e ) {
        e.preventDefault();
        $( 'body' ).upiDialog( 'Remove all items from your cart?', {
            no: null,
            yes: function () {
                window.location = e.target.href;
            }
        } );
    },
    handle_submit = function ( e ) {
        if ( 'keypress' === e.type && e.which !== 13 ) {
            return true;
        }

        var target_button, radioList, isRadioButton;

        if ( 'keypress' === e.type ) {
            // find the closest action button for given input target
            target_button = $( e.target )
                .closest( '.paddedBoxContent, #promoCodeBox' )
                .find( '.secondary' );
        }
        else {
            radioList = [].concat(
                this.shipping_buttons.toArray(),
                this.shipping_address_buttons.toArray(),
                this.billing_address_buttons.toArray()
            );
            isRadioButton = $.inArray( e.target, radioList ) !== -1;
            target_button = isRadioButton ? null : e.target;
        }

        if ( !isRadioButton ) {
            // if not a post by radio button - prevent default to stop double post at checkout in IE
            e.preventDefault();
        }

        // shipping radio buttons will trigger a cart update
        // if event target is part of update_controls collection then the form should be updated
        // else event target was a submit control
        this.trigger(
            $.inArray( e.target, this.submit_buttons ) === -1
                ? 'cart:update'
                : 'cart:submit',
            target_button
        );
    },
    /* toggle order totals and active button to indicate page is being updated to user.
     disable submit buttons to prevent multiple posts. */
    update = function ( e, button ) {
        var form = this.el;

        if ( this.is_disabled() ) {
            return false;
        }

        if ( $.inArray( button, this.submit_buttons ) === -1 ) {
            this.order_totals
                .addClass( 'updating' )
                .find( 'h2' )
                .text( 'Updating Totals' );
        }

        this.toggle_buttons(
            button ? {target: $( button ), text: 'Updating'} : null
        );
        // patch: safari halts all execution on form post - need short delay in order to display ui processing indicators
        setTimeout( function () {
            form.submit();
        }, 200 );
    },
    submit = function ( e, button ) {
        var form = this.el;
        if ( this.is_disabled() || !this.is_valid() ) {
            return false;
        }

        if ( is_payment( button ) ) {
            show_processing();
        }
        this.toggle_buttons();
        this.el.append( create_post_field( button ) );
        // patch: safari halts all execution on form post - need short delay in order to display ui processing indicators
        setTimeout( function () {
            form.submit();
        }, 200 );
    },
    is_valid = function () {
        return this.el.valid();
    },
    is_disabled = function () {
        return this.disabled;
    },
    /* private */
    is_payment = function ( button ) {
        return button && !!$( button ).val().match( 'Complete Payment' );
    },
    show_processing = function () {
        $( 'body' ).prepend( upi.html.processOrderDialog() );
    },
    create_post_field = function ( target ) {
        if ( !target ) {
            throw new TypeError( 'POST cart error: "target" in not defined' );
        }

        var input = document.createElement( 'input' );
        input.type = 'hidden';
        input.name = target.name;
        input.value = target.value;
        return input;
    },
    update_print_preview = function () {
        var $preview = this.print_preview,
            rootUrl = $preview.attr( 'src' ).split( '?' )[0],
            query = [],
            $inputs = this.print_inputs;

        $inputs.each( function () {
            query.push(
                [this.name, encodeURIComponent( this.value.trim() )].join( '=' )
            );
        } );

        $preview.attr( 'src', rootUrl + '?' + query.join( '&' ) );
    };

const CartModule = Class.extend(
    $.extend(
        {
            validate_fields: {
                ctl00$ContentPlaceHolder1$cardNumberRadio: {
                    required: true
                },
                firstName: {required: true, minlength: 2},
                lastName: {required: true, minlength: 2},
                cardNumber: {required: true, creditcard: true},
                cardType: {required: true},
                expirationMonth: {
                    required: true,
                    range: [1, 12],
                    minlength: 2,
                    maxlength: 2
                },
                expirationYear: {
                    required: true,
                    minlength: 2,
                    maxlength: 2,
                    range: [new Date().getFullYear() - 2000, 99]
                }
            },

            events: {
                checkoutpageshow: 'handle_pageshow',
                'click delete_cart_button': 'handle_delete_cart',
                'change shipping_buttons': 'handle_submit',
                //            'click address_buttons': 'handle_submit',
                'change billing_address_buttons': 'handle_submit',
                'change shipping_address_buttons': 'handle_submit',
                'click update_buttons': 'handle_submit',
                'click submit_buttons': 'handle_submit',
                'keypress update_fields': 'handle_submit',
                'keyup print_inputs': 'update_print_preview',
                'click change_address_buttons': 'handle_address_change',
                'change address_inputs': 'select_address_item',
                'change toggle_custom_printing': 'toggle_custom_printing'
            },

            elements: {
                'input[name$="UpdateCart_Button"], input[name$="PromoCode_Button"]': 'update_buttons',
                '.shippingOptions [type="radio"]': 'shipping_buttons',
                //            '#billingAddressContainer input[type="radio"], #shippingAddressContainer input[type="radio"]': 'address_buttons',
                '#billingAddressContainer input[type="radio"]': 'billing_address_buttons',
                '#shippingAddressContainer input[type="radio"]': 'shipping_address_buttons',
                'input[name^="skuQty"], input[name$="PromoCode_TextBox"]': 'update_fields',
                'input[name*="ContinueCheckout_Button"], input[name*="Submit_Button"]': 'submit_buttons',
                '#orderSummaryContainer': 'order_totals',
                '#customPrintingContent input': 'print_inputs',
                '#preview': 'print_preview',
                '#billingAddressContainer, #shippingAddressContainer': 'address_containers',
                '#cart .delete-cart': 'delete_cart_button',
                '#cart .addressList': 'address_lists',
                '#cart .button[title="Change"]': 'change_address_buttons',
                '#cart .addresses input[type="radio"]': 'address_inputs',
                '#customPrintSetup': 'custom_print_container',
                '#doCustomPrint': 'custom_print_checkbox'
            },

            init: init,
            handle_delete_cart: handle_delete_cart,
            handle_submit: handle_submit,
            handle_pageshow: handle_pageshow,
            toggle_custom_printing: toggle_custom_printing,
            toggle_buttons: toggle_buttons,
            toggle_address_list: toggle_address_list,
            enable_submit: enable_submit,
            update: update,
            submit: submit,
            is_valid: is_valid,
            is_disabled: is_disabled,
            update_print_preview: update_print_preview,
            select_address_item: select_address_item,
            handle_address_change: handle_address_change
        },
        Module,
        Events
    )
);

$( document ).ready( function () {
    // also disables and child form elements associated with radio buttons
    $( '.addresses, #paymentOptionContainer' )
        .toggleRadioSelect()
        .on( 'radio:select', function ( e, selection ) {
            if ( Modernizr.mq( upi.mq.max.screenMedium ) ) {
                $( selection ).scrollToElement();
            }
        } );

    // add "checkmark" to heading if order submitted successfully
    $( '#orderCompleteMsg h2:contains("submitted successfully")' ).addClass(
        'success'
    );

    // FedEx shipping map
    $( '#shippingOptionsContainer .colorbox' ).colorbox(
        upi.settings.defaultColorbox()
    );

    // add print invoice button to invoice header
    $( '#ReviewItemContainer .mod-hd' ).prepend(
        upi.html.printButton( 'Print Invoice' )
    );

    // patch: extend length of promofield
    $( '#promoField' ).attr( 'maxlength', 36 );

    $( '#aspnetForm, #form1' ).each( function ( i, item ) {
        var cartmod = new CartModule( item );
    } );

    // display Holiday Shipping message
    if ( upi.conf.SHOW_HOLIDAY_SHIPPING_NOTICE ) {
        var msg = upi.conf.HOLIDAY_SHIPPING_NOTICE;
        var $msgBar = $(
            '<div class="paddedBlock notice"><p class="msg"></p></div>'
        );
        $msgBar.find( '.msg' ).text( msg );
        $msgBar.insertBefore( '.cartFooter' );
    }
} );



